import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { ApiUrlViewModel } from "../../Model/ViewModel/ApiUrlViewModel";

/**
 * Generated class for the ApiUrlModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-api-url-add-edit-modal",
  templateUrl: "api-url-add-edit-modal.html"
})

export class ApiUrlAddEditModalPage {
  item: ApiUrlViewModel;
  mode: string;

  selectOptions:any;
  title:string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController
  ) {
    let change_mode = navParams.data.change_mode;
    this.item = new ApiUrlViewModel();
    this.item.id = navParams.data.item.id;
    if(change_mode==="c")
      this.item.is_default = false;
    else
      this.item.is_default = navParams.data.item.is_default;
    this.item.disp_name= navParams.data.item.disp_name;
    this.item.url=navParams.data.item.url;
    this.item.is_url_forward=navParams.data.item.is_url_forward;
    this.mode = navParams.data.mode;

   if(this.mode==="POST")
     this.title="新增";
   else
     this.title="更新";

  }

   Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad ApiUrlModalPage");
  }
}



